<template>
  <div color="snake-container">
    <table v-if="!gameOver" class="align-center justify-center text-center snake-table" cellspacing="0" cellpadding="0">
      <tr v-for="y in gridSize" :key="y">
          <td v-for="x in gridSize" :key="`${x}${y}`" class="grid-cell" :class="{
            snake1: snakes[0] && snakes[0].active && snakes[0].body.includes(`${x} ${y}`), 
            snake2: snakes[1] && snakes[1].active && snakes[1].body.includes(`${x} ${y}`),
            snake3: snakes[2] && snakes[2].active && snakes[2].body.includes(`${x} ${y}`),
            snake4: snakes[3] && snakes[3].active && snakes[3].body.includes(`${x} ${y}`),
            food: food.includes(`${x} ${y}`)
          }">
          </td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'Snake',
  props: ['socket','players','ts'],
  data: () => ({
    gameOver: false,
    intervals: [],
    snakes: [
      {
        body: ['3 5','3 4','3 3','3 2'],
        direction: 'D',
        active: false,
        color: '#4DB6AC'
      },
      {
        body: ['38 35','38 36','38 37','38 38'],
        direction: 'U',
        active: false,
        color: "#64B5F6"
      },
      {
        body: ['6 38','5 38','4 38','3 38'],
        direction: 'R',
        active: false,
        color: "#E57373"
      },
      {
        body: ['35 3','36 3','37 3','38 3'],
        direction: 'L',
        active: false,
        color: "#FFEE58"
      },
    ],
    food: ['5 5', '15 4'],
    gridSize: 40
  }),

  created() {
    const activeSnakes = this.players.map(p => p.position)
    this.snakes = this.snakes.map((s,ind) => activeSnakes.includes(ind) ? {...s, active: true} : s)
    this.activeSnakes.forEach((snake,id) => {
      this.intervals[id] = setInterval(() => {
        this.moveSnake(id,snake.direction)
      }, 100)    
    })
    this.placeFood(0)
    this.placeFood(1)

    this.socket.on('playerMove', (data) => {
      console.log('playermove',data)
      if (data.type === 'moveSnake') {
        console.log('SNAKE MOVE', new Date(this.ts.now()).toISOString().replace(/[A-Z]/g, ' '))
        var stats = document.querySelector("#stats")
        var newNode = document.createElement('div');      
        newNode.innerHTML = ('Peer ' + data.playerId + new Date(this.ts.now()).toISOString().replace(/[A-Z]/g, ' '));
        stats.appendChild(newNode)

        const player = this.players.filter(p => p.id == data.playerId)[0]
        const currentPosition = this.snakes[player.position].direction 
        if (['L','R'].includes(data.direction)) {
          this.snakes[player.position].direction = ['U','D'].includes(currentPosition) ? data.direction : currentPosition
        } else if (['U','D'].includes(data.direction)) {
          this.snakes[player.position].direction = ['L','R'].includes(currentPosition) ? data.direction : currentPosition
        }
      }
    })
  },

  computed: {
    activeSnakes() {
      return this.snakes.filter(s => s.active)
    }
  },

  watch: {
    activeSnakes() {
      if (!this.activeSnakes.length) {
        this.$emit('gameOver')
      }
    }
  },

  methods: {
      //Moved the whole body of the snake
      moveSnake(snakeId, direction) {
        console.log('s',snakeId, direction)
        const snakeBody = [...this.snakes[snakeId].body]
        console.log('t',snakeBody)

        if (Number(snakeBody[0].split(' ')[0]) === 1 && this.snakes[snakeId].direction === 'L' || 
            Number(snakeBody[0].split(' ')[0]) === this.gridSize && this.snakes[snakeId].direction === 'R' ||
            Number(snakeBody[0].split(' ')[1]) === 1 && this.snakes[snakeId].direction === 'U' || 
            Number(snakeBody[0].split(' ')[1]) === this.gridSize && this.snakes[snakeId].direction === 'D' ||
            this.isSnakeHit(snakeId, snakeBody[0])) {
              clearInterval(this.intervals[snakeId])
              this.blink(snakeId)
              return
        }
        this.snakes[snakeId].body[0] = this.moveHead(this.snakes[snakeId].body[0], direction)
        this.snakes[snakeId].body = this.snakes[snakeId].body.map((x,i,arr) => (i === 0) ? arr[0] : snakeBody[i - 1])
        this.snakes[snakeId].direction = direction

        if (this.food.includes(snakeBody[0])) {
          this.snakes[snakeId].body.push(snakeBody[snakeBody.length - 1])
          this.placeFood(this.food.indexOf(snakeBody[0]))
          this.$emit('updateScore',snakeId)
        }
      },

      //Moves the head of the snake based on the direction
      moveHead(cell, direction) {
        const cellArr = cell.split(' ')
        switch (direction) {
          case 'D': return `${cellArr[0]} ${(Number(cellArr[1]) + 1)}`
          case 'U': return `${cellArr[0]} ${(Number(cellArr[1]) - 1)}`
          case 'L': return `${(Number(cellArr[0]) - 1)} ${cellArr[1]}`
          case 'R': return `${(Number(cellArr[0]) + 1)} ${cellArr[1]}`
        }
      },

      //Blinking after a snake hit a wall or another snake
      blink(snakeId) {
        const snake = document.querySelectorAll(`.snake${snakeId + 1}`);
        snake.forEach(cell => {
          let blinks = 0;
          let intervalId = setInterval(() => {
            if (cell.style.visibility === 'hidden') {
                cell.style.visibility = 'visible';
                if (blinks++ === 2) {
                    clearInterval(intervalId);
                    this.killSnake(snakeId)
                }
            } else {
                cell.style.visibility = 'hidden';
            }    
          }, 300);
        })
      },

      //Places food on a new position
      placeFood(foodInd) {
        let foodPlaced = false
        let x = 0
        let y = 0
        while (!foodPlaced) {
          x = Math.floor(Math.random() * this.gridSize) + 1
          y = Math.floor(Math.random() * this.gridSize) + 1
          foodPlaced = true
          this.activeSnakes.forEach(snake => {
            if (snake.body.includes(`${x} ${y}`)){
              foodPlaced = false
            }
          })
        }
        this.food[foodInd] = `${x} ${y}`
      },

      isSnakeHit(snakeId, cell) {
        let isHit = false
        this.activeSnakes.forEach( (snake,ind) => {
          if (snakeId !== ind && snake.body.includes(cell)) {
            isHit = true
          }
        })
        return isHit
      },

      killSnake(snakeId) {
        this.snakes[snakeId].active = false
      }
  }
}
</script>

<style scoped>

td {
  background-color: #263238;
}

.snake1 {
  background-color: #4DB6AC !important;
}

.snake2 {
  background-color: #E57373 !important;
}

.snake3 {
  background-color: #64B5F6 !important;
}

.snake4 {
  background-color: #FFEE58 !important;
}

.food {
  background-color: #EC407A;
}

.snake-table {
    width: 80vh;
    margin: auto;
    height: 80vh;
    background-color: #263238;
    /* margin-top: 2%;
    padding-top: 2%; */
}

.snake-container {
  width: 100%;
  height: 100%;
  /* margin-top: 2%;
  padding-top: 2%; */
}

</style>
