<template>
    <div class="controls">
        <v-card v-if="roomFull" class="player-btn red darken-4 white--text" dark>
            WAIT FOR A FREE SPOT 
        </v-card>

        <template v-else>
            <template v-if="roomState !== 'gameStarted' && roomState !== 'gameCountdown'">
                <v-card v-if="playerState === 'connected'" class="player-btn deep-purple darken-4 white--text" dark @click="playerReady">
                    READY
                </v-card>
                <v-card v-if="playerState === 'ready' && roomState === 'roomReady'" class="player-btn deep-purple darken-4 white--text" dark @click="playerStartGame">
                    START GAME
                </v-card>
                <v-card v-if="playerState === 'ready' && roomState === 'roomOpened'" class="player-btn  deep-purple darken-4 white--text" dark>
                    WAITING FOR OTHER PLAYERS...
                </v-card>
            </template>
            <template v-if="roomState === 'gameStarted' || roomState === 'gameCountdown'">
                <v-card v-if="playerState !== 'playing' && playerState !== 'ready'" class="player-btn red darken-4 white--text" dark>
                    WAIT FOR ANOTHER ROUND 
                </v-card>
                <template v-if="playerState === 'playing' || playerState === 'ready'">
                    <SnakeControls v-if="selectedGame === 'snake'" :playerColor="playerColor" @moveSnake="playerMove"/>
                </template>
            </template>
        </template>
        <p id="stats"/>
    </div>

</template>

<script>
import Peer from 'peerjs'
import SnakeControls from './controls/SnakeControls.vue'
import * as timesync from 'timesync'

export default {
  name: 'Controls',
  components: {
    SnakeControls
  },
  props: ['selectedGame','screenId','socket'],
  data: () => ({
    playerId: null,
    playerState: 'connecting', //connected, ready, playing, gameOver
    roomState: '',
    roomFull: false,
    playerColor: '#FFFFFF',
    publicScreenId: '',
    ts: null
  }),
  created() {
    if (this.screenId) {
        this.publicScreenId = this.screenId
        this.joinRoom()
    } else {
        this.publicScreenId = prompt("Please enter the screen's peer ID:")
        this.joinRoom()
    }

    var ts = timesync.create({
      server: this.socket,
      interval: 5000
    });
    ts.on('sync', function (state) {
      console.log('sync ' + state + '');
    });
    ts.on('change', function (offset) {
      console.log('changed offset: ' + offset + ' ms');
    });
    ts.send = (socket, data, timeout) => {
      //console.log('send', data);
      this.socket.emit('timesync', data)
    //   return new Promise( (resolve, reject) => {
    //     var timeoutFn = setTimeout(reject, timeout);
    //     this.socket.emit('timesync', data, function () {
    //       clearTimeout(timeoutFn);
    //       resolve();
    //     });
    //   });
    // Ignoring timeouts
        return Promise.resolve();
    };
    this.ts = ts
    this.socket.on('timesync', function (data) {
      //console.log('receive', data);
      ts.receive(null, data);
    });

    this.socket.on('roomState', roomState => {
        console.log('roomState',roomState)
        this.roomState = roomState
    })

    this.socket.on('noSuchRoom', () => {
        console.log('No room with such ID.')
        this.$emit('mainScreen')
    })

    this.socket.on('roomFull', () => {
        console.log('Room full')
        this.roomFull = true
    })

    this.socket.on('playerState', (playerState) => {
        console.log('Player state: ', playerState)
        this.playerState = playerState
    })

    this.socket.on('playerId', (playerId) => {
        console.log('Player id: ', playerId)
        this.playerId = playerId
    })

    this.socket.on('playerInfo', (player) => {
        console.log('Player: ', player)
        this.playerColor = player.color
    })
  },

  methods: {
    playerMove (data) {
        console.log('SNAKE MOVE', new Date(this.ts.now()).toISOString().replace(/[A-Z]/g, ' '))
        //Stats
        var stats = document.querySelector("#stats")
        var newNode = document.createElement('div');      
        newNode.innerHTML = ('Peer ' + this.playerId + new Date(this.ts.now()).toISOString().replace(/[A-Z]/g, ' '));
        stats.appendChild(newNode)
        this.socket.emit('playerMove', {...data, playerId: this.playerId, screenId: this.screenId})
    },

    playerReady() {
        //this.fullscreenMode()
        this.socket.emit('playerState', {id: this.playerId, screenId: this.screenId, state:'ready'})
    },

    playerStartGame () {
        this.socket.emit('startGame', this.screenId)
    },

    joinRoom() {
        this.socket.emit('joinRoom', this.publicScreenId)
    },

    fullscreenMode () {
        const docElem = document.documentElement
        /* View in fullscreen */
        if (docElem.requestFullscreen) {
            docElem.requestFullscreen();
        } else if (docElem.mozRequestFullScreen) { /* Firefox */
            docElem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
            docElem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) { /* IE/Edge */
            docElem.msRequestFullscreen();
        }

        screen.orientation.lock("landscape-primary")

    }

  }
}
</script>

<style>

.controls {
    width: 100%;
    height: 100%;
}

.player-btn {
  width: 100%;
  height: 100% !important;
  font-size: 10vw !important;
  padding-top: 10%;
}

</style>

