<template>
    <div class="game-room">
        <v-row class="main-row">
            <v-col cols="12">
            <v-card class="main-card">
                <v-row class="game-room-row">
                    <v-col cols="8" class="justify-center align-center text-center game-screen">

                        <v-card class="game-card">

                            <div v-if="['roomOpened','roomReady','gameCountdown', 'gameEnded'].includes(roomState)" class="waiting-card deep-purple darken-4 white--text">

                                <template v-if="roomState === 'roomOpened'">
                                    <h1>WAITING FOR PLAYERS...</h1>
                                    <br>
                                    <p>Scan the QR code or use the SCREEN ID to join the game:</p>
                                    <br>
                                    <img alt="QR code" :src="qrCodeUrl">
                                    <br>
                                    SCREEN ID: {{ screenId }}
                                    <br><br>
                                    <h3>CLICK 'READY' TO JOIN THE NEXT ROUND OF {{selectedGame.toUpperCase()}}</h3>
                                </template>
                                
                                <template v-if="roomState === 'roomReady'">
                                    <h3>CLICK 'READY' TO JOIN THE NEXT ROUND OF {{selectedGame.toUpperCase()}}</h3>
                                    <v-btn large class="mt-5 start-btn" color="white deep-purple--text" @click="startGameCountDown">START GAME</v-btn>
                                    <br>
                                    <br>
                                    <br>
                                    <p>Scan the QR code or use the PEER ID to join the game:</p>
                                    <br>
                                    <img alt="QR code" :src="qrCodeUrl">
                                    <br>
                                    SCREEN ID: {{ screenId }}
                                </template>

                                <template v-if="roomState === 'gameCountdown'">
                                    <h1>GAME STARTING IN</h1>
                                    <h1>{{countDown}}</h1>
                                </template>

                                <template v-if="roomState === 'gameEnded'">
                                    <h1>RESULTS:</h1>
                                    <v-row class="results-row">
                                        <v-list>
                                            <v-list-item
                                            v-for="player in results"
                                            :key="player.id"
                                            >
                                            <v-list-item-icon>
                                                <v-icon :color="player.color">person</v-icon>
                                            </v-list-item-icon>

                                            <v-list-item-content>
                                                <v-list-item-title>Player {{player.id}} - {{player.score}}</v-list-item-title>
                                            </v-list-item-content>

                                            </v-list-item>
                                        </v-list>
                                    </v-row>
                                    <v-btn large class="mt-5 start-btn new-game-btn" color="white deep-purple--text" @click="newGame">PLAY AGAIN</v-btn>

                                </template>

                            </div>                                

                            <Snake v-if="roomState === 'gameStarted' && selectedGame === 'snake'" 
                                    :players="players.filter(p => p.state === 'playing')"
                                    :socket="socket"
                                    :ts="ts"
                                    @updateScore="updateScore"
                                    @gameOver="gameOver"/>

                        </v-card>

                    </v-col>
                    <v-col cols="4" class="align-end">
                        <div class="stats-side">
                            <v-row>
                                <h3>Players ({{ sortedPlayingPlayers.length }})</h3>
                            </v-row>
                            <v-row>
                                <v-list>
                                    <v-list-item
                                    v-for="player in sortedPlayingPlayers"
                                    :key="player.id"
                                    >
                                    <v-list-item-icon>
                                        <v-icon :color="player.color">person</v-icon>
                                    </v-list-item-icon>

                                    <v-list-item-content>
                                        <v-list-item-title>Player {{player.id}} ({{player.state}}) - {{player.score}}</v-list-item-title>
                                    </v-list-item-content>

                                    </v-list-item>
                                </v-list>
                            </v-row>
                            <v-row>
                                <h3>Viewers ({{ players.filter(p => p.state !== 'playing' && p.state !== 'ready').length }})</h3>
                            </v-row>
                            <v-row>
                                <v-list>
                                    <v-list-item
                                    v-for="player in players.filter(p => p.state !== 'playing' && p.state !== 'ready')"
                                    :key="player.id"
                                    >
                                    <v-list-item-icon>
                                        <v-icon :color="player.color">person</v-icon>
                                    </v-list-item-icon>

                                    <v-list-item-content>
                                        <v-list-item-title>Player {{player.id}} ({{player.state}}) </v-list-item-title>
                                    </v-list-item-content>

                                    </v-list-item>
                                </v-list>
                            </v-row>
                        </div>
                        <div v-if="roomState === 'gameStarted' || roomState === 'gameCountdown' || roomState === 'gameEnded'" class="qr-code-side">
                            <v-row class="row-100-width">
                                <p class="margin-auto">Scan the QR code to join the game:</p>
                            </v-row>
                            <v-row class="row-100-width">
                                <img alt="QR code" :src="qrCodeUrl"  class="margin-auto">
                            </v-row>
                            <v-row class="row-100-width">
                                <p  class="margin-auto">SCREEN ID: {{ screenId }}</p>
                            </v-row>
                        </div>
                    </v-col>
                </v-row>
            </v-card>
            </v-col>
        </v-row>
        <p id="stats"/>
    </div>

</template>

<script>
import Peer from 'peerjs'
import Snake from './games/Snake.vue'
import * as timesync from 'timesync'

export default {
  name: 'GameRoom',
  components: {
    Snake
  },
  props: ['selectedGame','screenId','socket'],
  data: () => ({
    //Games
    games: {
        snake: {
            defaultScore: 0,
            minPlayers: 1,
            maxPlayers: 4
        },
        poker: {
            defaultScore: 1000,
            minPlayers: 1,
            maxPlayers: 10
        }
    },
    playerColors: ["#4DB6AC","#E57373","#64B5F6","#FFEE58","#BA68C8","#FFB74D","#4DB6AC","#F06292","#80DEEA","#AED581"],
    //Current game data
    roomState: 'roomOpened', // roomOpened, roomReady, gameCountDown, gameStarted, gameEnded
    players: [],
    countDown: 0,
    ts: null
  }),
  created() {

    var ts = timesync.create({
      server: this.socket,
      interval: 5000
    });
    ts.on('sync', function (state) {
      console.log('sync ' + state + '');
    });
    ts.on('change', function (offset) {
      console.log('changed offset: ' + offset + ' ms');
    });
    ts.send = (socket, data, timeout) => {
      //console.log('send', socket, data);
      this.socket.emit('timesync', data)
    //   return new Promise( (resolve, reject) => {
    //     var timeoutFn = setTimeout(reject, timeout);
    //     this.socket.emit('timesync', data, function () {
    //       clearTimeout(timeoutFn);
    //       resolve();
    //     });

        // Ignoring timeouts
        return Promise.resolve();
      //});
    };
    this.ts = ts
    this.socket.on('timesync', function (data) {
      //console.log('receive', data);
      ts.receive(null, data);
    });

    this.socket.on('players', (players) => {
        console.log('Players', players)
        this.players = players
    })

    this.socket.on('startGame', () => {
        console.log('Start game')
        this.startGameCountDown()
    })

  },
  computed: {
    qrCodeUrl: function() {
        if (!this.privateScreen) {
            return "https://api.qrserver.com/v1/create-qr-code/?data=" + 'https://stubnovalucia.github.io/king-sombra-games/' + '?game=' + this.selectedGame + '%26screenid=' + this.screenId + "&size=200x200"
        }
    },
    readyPlayers() {
        return this.players.filter(p => p.state === 'ready')
    },
    sortedPlayingPlayers() {
        const playingPlayers = this.players.filter(p => p.state === 'playing' || p.state === 'ready')
        playingPlayers.sort((p1,p2) => p2.score - p1.score)
        return playingPlayers
    }
  },
  watch: {
    readyPlayers() {
      if (this.readyPlayers.length >= this.games[this.selectedGame].minPlayers) {
          this.roomState = 'roomReady'
          this.socket.emit('roomState', {id: this.screenId, state:this.roomState})
      }
    },
    roomState () {
        this.socket.emit('roomState', {id: this.screenId, state:this.roomState})
    },
  },
  methods: {

    startGameCountDown() {
        this.roomState = 'gameCountdown'
        this.countDown = 10
        let counDownInterval = setInterval(() => {
        this.countDown -= 1
        if (this.countDown === 0) {
            clearInterval(counDownInterval);
            this.startGame()
        }
        },1000)
    },

    startGame() {
        this.players = this.players.map(p => p.state === 'ready' ? {...p, state: 'playing'} : p)
        this.roomState = 'gameStarted';
    },

    updateScore(position) {
        this.players = this.players.map(p => p.position === position ? {...p, score: p.score + 10} : p)
    },

    gameOver() {
        this.results = [...this.sortedPlayingPlayers]
        this.players = this.players.map(p => {
            return {...p, color: '#546E7A', state: 'connected', score: this.games[this.selectedGame].defaultScore}
        })
        this.roomState = 'gameEnded'
    },

    newGame () {
        this.roomState = 'roomOpened'
    }

  }
}
</script>

<style>
.main-card, .main-row {
  height: 100%
}

.game-room {
    width: 100%;
    height: 100%;
}

.game-screen {
    height: 100%;
}

.game-card {
    margin-left: 5%;
    width: 90%;
    height: 95%;
}

.game-room-row {
    height: 100%;
} 

.waiting-card {
    width: 100%;
    height: 100%;
    padding-top: 10%;
}

.start-btn {
    width: 300px !important;
    height: 100px !important;
    font-size: 2vw !important;
    background-color: white !important;
}

.results-row {
    width: 100%;
    padding-top: 5%;
    margin: 0 !important;
}

.results-row .v-list {
    margin: auto;
}

.stats-side {
    height: 60%;
    overflow-y: auto;
    overflow-x: hidden;
    padding-left: 4%;
}

.qr-code-side {
    height: 40%;
}

.row-100-width {
    width: 100%;
}

.margin-auto {
    margin: auto;
}

.new-game-btn {
    margin-top: 7% !important;
}

.v-list-item__title {
    text-align: left;
}

</style>

