<template>
    <div id="app">
      <v-app class="ma-0">

        <v-container fluid class="main-container text-center">

          <template v-if="state === 'gameSelected' || state === 'gameSelection'">
            <h1 class="mt-5">KING SOMBRA GAMES {{selectedGame ? ' - ' + selectedGame.toUpperCase() : ''}}</h1>
            <v-row>
              <v-col cols="4" offset="2">
                <v-card v-if="state === 'gameSelection'" color="deep-purple darken-4" class="choose-game-btn" dark @click="selectGame('snake')">SNAKE</v-card>
                <v-card v-if="state === 'gameSelected'" color="deep-purple darken-4" class="choose-game-btn" dark @click="openRoom">START</v-card>
              </v-col>
              <v-col cols="4">
                <v-card v-if="state === 'gameSelection'" color="deep-purple darken-4" class="choose-game-btn" dark @click="selectGame('poker')">POKER</v-card>
                <v-card v-if="state === 'gameSelected'" color="deep-purple darken-4" class="choose-game-btn" dark @click="joinRoom">JOIN</v-card>
              </v-col>
            </v-row>
          </template>

          <template v-if="state === 'roomOpened'">
            <GameRoom v-if="!privateScreen" :selectedGame="selectedGame" :screenId="screenId" :socket="socket" @mainScreen="roomSelection"/>
            <Controls v-if="privateScreen" :selectedGame="selectedGame" :screenId="screenId" :socket="socket" @mainScreen="roomSelection"/>
          </template>

        </v-container>
      </v-app>
    </div>
</template>

<script>
import GameRoom from './components/GameRoom.vue'
import Controls from './components/Controls.vue'

import io from 'socket.io-client';

export default {
  name: 'app',
  components: {
    GameRoom,
    Controls
  },

  data: () => ({
    selectedGame: '',
    privateScreen: false,
    screenId: '',
    state: 'gameSelection',
    socket: null,
  }),
  created() {
    if (this.$route.query.game && this.$route.query.screenid) {
      this.state = 'roomOpened'
      this.selectedGame = this.$route.query.game
      this.screenId = this.$route.query.screenid
      this.privateScreen = true
    }

    this.socket = io('178.128.194.208:80')
    this.socket.on('connected', (data) => {
      console.log(data);
      this.socket.emit('reply', { my: 'data' });
    });

    this.socket.on('roomOpened', (screenId) => {
      this.screenId = screenId
      this.state = 'roomOpened'
      console.log(screenId);
    });

  },
  methods: {
    selectGame (game) {
        this.selectedGame = game
        this.state = 'gameSelected'
    },

    openRoom () {
      //this.state = 'roomOpened'
      this.socket.emit('openRoom', 'snake');
      this.fullscreenMode()
    },

    joinRoom () {
      this.state = 'roomOpened'
      this.privateScreen = true
    },

    roomSelection () {
      this.state = 'gameSelected'
      this.privateScreen = false
    },

    fullscreenMode () {
      const docElem = document.documentElement
      /* View in fullscreen */
      if (docElem.requestFullscreen) {
          docElem.requestFullscreen();
      } else if (docElem.mozRequestFullScreen) { /* Firefox */
          docElem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
          docElem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) { /* IE/Edge */
          docElem.msRequestFullscreen();
      }
    }

  }
}
</script>

