<template>
    <v-row class="main-row">
        <v-col cols="12" class="main-col">
            <v-row class="row-100" justify="center">
            <v-col cols="4" justify="center">
                <v-card class="control-card side-control" :color="playerColor" @click="moveSnake('L')">
                <v-icon>
                    keyboard_arrow_left
                </v-icon>
                </v-card>
            </v-col>
            <v-col cols="4">
