                    </v-icon>
                </v-card>
                </v-row>
                <v-row class="row-50 mt-2">
                <v-card class="control-card middle-control" :color="playerColor" @click="moveSnake('D')">  
                    <v-icon>
                    keyboard_arrow_down
                    </v-icon>
                </v-card>
                </v-row>
            </v-col>
            <v-col cols="4">
                <v-card class="control-card side-control" :color="playerColor" @click="moveSnake('R')">  
                <v-icon>
                    keyboard_arrow_right
                </v-icon>
                </v-card>
